import {SliderConstants} from '../constants';

export default class AdaptiveController {
    constructor() {
        this.pageWidth = window.innerWidth;
        this.slidesOnScreen = AdaptiveController.maxSlidesOnScreen();
        this.listeners = [];
    }

    addListener(listener) {
        if (listener instanceof Array) {
            this.listeners = this.listeners.concat(listener);
        }
        else {
            this.listeners.push(listener);
        }
    }

    observePageSize() {
        const that = this;
        window.addEventListener('resize', function () {
            that.pageWidth = this.innerWidth;
            const maxSlidesOnScreen = AdaptiveController.maxSlidesOnScreen();

            if (that.slidesOnScreen !== maxSlidesOnScreen) {
                that.slidesOnScreen = maxSlidesOnScreen;
                that.listeners.forEach(listener => listener.pageSizeChanged(maxSlidesOnScreen));
            }
        })
    }

    static maxSlidesOnScreen() {
        return Math.floor(window.innerWidth / (SliderConstants.SLIDE_WIDTH + SliderConstants.SLIDE_MARGIN)) || 1;
    }
}



