import {SliderConstants} from '../constants';

export default class ViewSlider {
    constructor() {
        this.slider = document.querySelector('.videoSlider');
        this.sliderWrapper = document.querySelector('.videoSliderWrapper');
    }

    addSlideToSlider(slideData,slidesAmount) {
        const slideList = document.createElement('li');
        slideList.classList.add('slide');
        slideList.setAttribute('data-slide',slidesAmount);
        const slideInfo = ` <div class="imageWrapper">
                                <img class="slidePreview" src="${slideData.previewSrc}" alt="preview">
                                <h2 class="slideTitle"><a class="slideLink" href="${slideData.link}" target="_blank">${slideData.title}</a></h2>
                            </div>
                            <div class="slideAuthor">${slideData.author}</div>
                            <div class="slideInfo">
                                <div class="infoWrapper clearfix">
                                    <div class="left_StatisticsInfo slideDatePublication">${slideData.publicationDate.slice(0, 10)}</div>
                                    <div class="right_StatisticsInfo slideViewsAmount">${slideData.viewsAmount}</div>
                                </div>
                                <div class="slideDescription">${slideData.descriptrion}</div>
                                <div class="infoWrapper likesWrapper clearfix">
                                    <div class="left_StatisticsInfo likesAmount">${slideData.likesAmount}</div>
                                    <div class="right_StatisticsInfo dislikesAmount">${slideData.dislikesAmount}</div>
                                </div>
                            </div>
                        `;
        slideList.innerHTML = slideInfo;

        this.slider.style.width = (SliderConstants.SLIDE_WIDTH + SliderConstants.SLIDE_MARGIN) * slidesAmount + 'px';
        this.slider.appendChild(slideList);
    }

    updatePosition(translate){
        this.slider.style.transform = `translate3d(${translate}px, 0px, 0px)`;
    }

    updateWrapper(width){
        this.sliderWrapper.style.width = `${width}px`;
    }

    resetSlider(){
        this.slider.style.transform = `translate3d(0px, 0px, 0px)`;
        this.slider.innerHTML = '';
    }
}




