import ViewPageLayout from '../views/ViewPageLayout';
import AdaptiveController from '../controllers/AdaptiveController';
import PaginationController from '../controllers/PaginationController';
import SliderController from '../controllers/SliderController';
import SwipeController from '../controllers/SwipeController';
import DataLoader from '../model/DataLoader';
import SearchController from '../controllers/SearchController';

const layout = new ViewPageLayout();
layout.createLayout();

const paginationController = new PaginationController();
const sliderController = new SliderController();

const adaptiveControler = new AdaptiveController();
adaptiveControler.addListener([paginationController,sliderController]);
adaptiveControler.observePageSize();

const dataLoader = new DataLoader();
dataLoader.addListener([paginationController,sliderController]);

paginationController.addLoader(dataLoader);
paginationController.addSliderController(sliderController);

const swipeController = new SwipeController();
swipeController.addSliderController(sliderController);
swipeController.addPaginationController(paginationController);

const searchController = new SearchController(dataLoader);





