import ViewPager from '../views/ViewPager';
import AdaptiveController from '../controllers/AdaptiveController';
import {PaginationConstants} from '../constants';

export default class PaginationController {
    constructor() {
        this.viewPager = new ViewPager();
        this.dataLoader = null;
        this.sliderController = null;
        this.currentPage = 1;
        this.slidesLoaded = 0;
        this.slidesOnScreen = 0;
        this.pagesAdded = 0;
        this.pager = document.querySelector('.pager');
        this.pager.addEventListener('click', this.handleClick.bind(this));
        this.visiblePages = {'firstVisible': 1, 'lastVisible': PaginationConstants.MAX_CONTROLS_ON_SCREEN};
        this.currentTranslateX = 0;
    }

    addSliderController(sliderController) {
        this.sliderController = sliderController;
    }

    addLoader(dataLoader) {
        this.dataLoader = dataLoader;
    }


    slideLoaded(slide) {
        this.slidesLoaded++;
        this.addControlIfNeeded();
    }

    addControlIfNeeded() {
        if (this.pagesAdded === 0 && this.slidesLoaded > 0) {
            this.pagesAdded++;
            this.viewPager.addPage(this.pagesAdded, true);
        }

        const maxSlidesOnScreen = AdaptiveController.maxSlidesOnScreen();
        this.slidesOnScreen = maxSlidesOnScreen;
        if (this.slidesLoaded > maxSlidesOnScreen
            && this.pagesAdded < Math.ceil(this.slidesLoaded / maxSlidesOnScreen)
            && this.currentPage === this.pagesAdded) {
            this.pagesAdded++;
            this.viewPager.addPage(this.pagesAdded, false);
            if (this.pagesAdded === this.currentPage + 1) {
                this.checkPagerPosition(this.currentPage);
            }
        }
    }

    checkForLoadingNewData() {
        const maxSlidesOnScreen = AdaptiveController.maxSlidesOnScreen();
        const mustLoadNewContent = Math.floor(this.slidesLoaded / maxSlidesOnScreen) <= this.currentPage + PaginationConstants.PAGE_DOWNGRADER_FOR_LOADING_NEW_DATA;
        this.currentPage++;
        if (mustLoadNewContent) {
            this.dataLoader.loadNewData();
        }
    }

    checkPagerPosition(currentPage) {
        if (this.pagesAdded > PaginationConstants.MAX_CONTROLS_ON_SCREEN && this.pagesAdded != currentPage) {
            let updateTransform = false;
            if (currentPage >= this.visiblePages.lastVisible && currentPage <= this.pagesAdded) {
                this.visiblePages.lastVisible = currentPage + 1;
                this.visiblePages.firstVisible = this.visiblePages.lastVisible - (PaginationConstants.MAX_CONTROLS_ON_SCREEN - 1);
                this.currentTranslateX = (this.visiblePages.lastVisible - PaginationConstants.MAX_CONTROLS_ON_SCREEN)
                    * (PaginationConstants.CONTROL_WIDTH + PaginationConstants.CONTROL_MARGIN);
                updateTransform = true;
            }
            if (currentPage == this.visiblePages.firstVisible && currentPage > 1) {
                this.visiblePages.firstVisible = currentPage - 1;
                this.visiblePages.lastVisible = this.visiblePages.firstVisible + (PaginationConstants.MAX_CONTROLS_ON_SCREEN - 1);
                this.currentTranslateX = this.currentTranslateX - PaginationConstants.CONTROL_WIDTH - PaginationConstants.CONTROL_MARGIN;
                updateTransform = true;
            }
            if (updateTransform) {
                this.viewPager.updateTransform(this.currentTranslateX);
            }
        }
    }

    /* pages clicks */
    handleClick(e) {
        const liArray = document.querySelectorAll('.pager .page');
        for (let i = 0; i < liArray.length; i++) {
            if (e.target === liArray[i]) {
                const li = e.target;
                this.activateCurrentPage(li);
                break;
            }
        }

    }

    simulateClickToPage() {
        const target = document.querySelector(`[data-page='${this.currentPage}']`);
        this.activateCurrentPage(target);
    }

    clickToNextPage(toNext) {
        if (toNext) {
            if (this.currentPage < this.pagesAdded) {
                this.checkForLoadingNewData();
                this.simulateClickToPage();
            }
        }
        else {
            if (this.currentPage > 1) {
                this.currentPage--;
                this.simulateClickToPage();
            }
        }
    }

    activateCurrentPage(target) {
        const activeSlide = document.querySelector('.pager .active');
        if (activeSlide) {
            activeSlide.className = 'page';
        }

        target.classList.add('active');
        const currentPage = Number(target.getAttribute('data-page'));
        if (currentPage > this.currentPage) {
            this.checkForLoadingNewData();
        } else {
            this.currentPage = currentPage;
        }
        this.sliderController.pageChanged(currentPage);
        this.addControlIfNeeded();
        this.checkPagerPosition(currentPage);
    }

    /*reset pagination*/

    recreatePages() {
        this.currentPage = 1;
        this.pagesAdded = 0;
        this.visiblePages = {'firstVisible': 1, 'lastVisible': PaginationConstants.MAX_CONTROLS_ON_SCREEN};
        this.currentTranslateX = 0;
        this.viewPager.clearContent();
        this.viewPager.updateTransform(this.currentTranslateX);
    }

    resetContent() {
        this.slidesLoaded = 0;
        this.recreatePages();
    }

    /*notifications*/

    pageSizeChanged(maxSlidesOnScreen) {
        if (!this.slidesLoaded) {
            return;
        }

        let currentSlide = this.slidesOnScreen * (this.currentPage - 1) + 1;
        if (currentSlide > this.slidesLoaded) {
            currentSlide--;
        }
        let pageForCurrentSlide = Math.ceil(currentSlide / maxSlidesOnScreen);
        if (pageForCurrentSlide === 0) {
            pageForCurrentSlide = 1;
        }
        this.recreatePages();
        let target = document.querySelector(`[data-page='${pageForCurrentSlide}']`);
        if (!target) {
            while (!target) {
                this.addControlIfNeeded();
                target = document.querySelector(`[data-page='${pageForCurrentSlide}']`);
                this.checkPagerPosition(this.currentPage);
                this.checkForLoadingNewData(); // means that page have never been exists
            }
        }
        this.activateCurrentPage(target);
    }
}



