import {PaginationConstants} from '../constants';

export default class ViewPager {
    constructor() {
        this.pager = document.querySelector('.pager');
        this.pagerWrapper = document.querySelector('.pagerWrapper');
        this.pagerWrapper.style.width = `${PaginationConstants.MAX_CONTROLS_ON_SCREEN * (PaginationConstants.CONTROL_WIDTH + PaginationConstants.CONTROL_MARGIN) - PaginationConstants.CONTROL_MARGIN}px`;
        this.pagerWrapper.style.height = `${PaginationConstants.CONTROL_WIDTH}px`;
    }

    addPage(pageNum, isActive) {
        const li = document.createElement('li');

        if (isActive) {
            li.classList.add('page', 'active');
        } else {
            li.classList.add('page');
        }

        li.innerHTML = pageNum;
        li.setAttribute('data-page', pageNum);
        this.pager.appendChild(li);
        this.pager.style.width = `${pageNum * (PaginationConstants.CONTROL_WIDTH + PaginationConstants.CONTROL_MARGIN)}px`;
    }

    clearContent() {
        this.pager.innerHTML = '';
    }

    updateTransform(transformValue){
        this.pager.style.transform = `translate3d(-${transformValue}px, 0px, 0px)`;
    }
}



