import ViewSlider from '../views/ViewSlider';
import AdaptiveController from '../controllers/AdaptiveController';
import {SliderConstants} from '../constants';

export default class SliderController{
    constructor(){
        this.viewSlider = new ViewSlider();
        this.slidesAmount = 0;
        this.slidesAmountOnScreen = 0;
        this.currentTranslateX = 0;
        this.currentTranslateXWithOffset = 0;
    }

    resetContent() {
        this.slidesAmount = 0;
        this.slidesAmountOnScreen = 0;
        this.currentTranslateX = 0;
        this.currentTranslateXWithOffset = 0;
        this.viewSlider.resetSlider();
    }

    slideLoaded(slide){
        this.slidesAmount++;
        this.viewSlider.addSlideToSlider(slide,this.slidesAmount);
        this.centerContent(AdaptiveController.maxSlidesOnScreen());
    }


    pageSizeChanged(maxSlidesOnScreen){
        this.centerContent(maxSlidesOnScreen);
    }

    centerContent(maxSlidesOnScreen) {
        if(!this.slidesAmount){
            return;
        }

        if (maxSlidesOnScreen !== this.slidesAmountOnScreen) {
            this.slidesAmountOnScreen = maxSlidesOnScreen;

            if (this.slidesAmountOnScreen < maxSlidesOnScreen) {
                this.currentTranslateX = this.currentTranslateX - SliderConstants.SLIDE_WIDTH - SliderConstants.SLIDE_MARGIN;
            } else if (this.slidesAmountOnScreen > maxSlidesOnScreen) {
                this.currentTranslateX = this.currentTranslateX + SliderConstants.SLIDE_WIDTH + SliderConstants.SLIDE_MARGIN;
            }

            const width = maxSlidesOnScreen * (SliderConstants.SLIDE_WIDTH + SliderConstants.SLIDE_MARGIN) - SliderConstants.SLIDE_MARGIN;
            this.viewSlider.updatePosition(this.currentTranslateX);
            this.viewSlider.updateWrapper(width);
        }
    }

    pageChanged(page) {
        const maxSlidesOnScreen = AdaptiveController.maxSlidesOnScreen();
        this.currentTranslateX = -(maxSlidesOnScreen * (page - 1) * (SliderConstants.SLIDE_WIDTH + SliderConstants.SLIDE_MARGIN));
        this.viewSlider.updatePosition(this.currentTranslateX);
    }

    sliderPositionChangedWithOffset(offset){
        this.currentTranslateXWithOffset = this.currentTranslateX+offset;
        this.viewSlider.updatePosition(this.currentTranslateX+offset);
    }

    fixSliderPosition(resultOffset){
        if(resultOffset > 0){
            this.currentTranslateX = 0;
            this.viewSlider.updatePosition(this.currentTranslateX);
        } else{
            const maxSliderTransformX = -(this.slidesAmount * (SliderConstants.SLIDE_WIDTH + SliderConstants.SLIDE_MARGIN) - (SliderConstants.SLIDE_WIDTH + SliderConstants.SLIDE_MARGIN) * this.slidesAmountOnScreen);
            if(this.currentTranslateXWithOffset < maxSliderTransformX){
                this.currentTranslateX = maxSliderTransformX;
                this.viewSlider.updatePosition(this.currentTranslateX);
            }
        }
    }
}



